@import "https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
  overflow-x: hidden;
}

:root {
  --header-bg-color: #01a390;
  --footer-bg-color: #01a390;
  --text-color: #000;
  --light-green: #00ad1d;
  --dark-green: #016034;
