  border-radius: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  font-weight: bold;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
  overflow-y: hidden;
  box-shadow: -1px 4px 20px 14px #0003;
}

.header .logo {
  background: #000;
  width: 200px;
  padding: 10px 0 10px 15px;
}

.nav-items > ul {
  justify-content: space-evenly;
  align-items: baseline;
  list-style-type: none;
  display: flex;
}

.nav-items > ul > li {
  cursor: pointer;
  padding: 10px;
  font-size: large;
}

.Link-tag {
  color: #fff;
  text-decoration: none;
}

.nav-items > ul > li .logout-btn, .nav-items > ul > li .login-btn {
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-right: 15px;
  padding: 10px;
  display: flex;
}

.nav-items > ul > li .logout-btn {
  background-color: var(--orange);
}

.nav-items > ul > li .login-btn {
  background-color: var(--light-red);
}

.nav-items > ul > li .logout-btn:hover, .nav-items > ul > li .login-btn:hover {
  background-color: var(--dark-green);
}

.search-container {
  text-align: center;
  max-width: 100%;
  margin: 30px auto 20px;
  position: relative;
}

.search-container .search-input {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  border: 1px solid #aabcca;
  border-right: 0;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  width: 40%;
  padding: 14px 15px 14px 20px;
  box-shadow: 1px 2px 4px #00000014;
}

.search-container .search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #0768b3;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  margin-left: -4px;
  padding: 15px 22px;
  box-shadow: 1px 2px 4px #00000014;
