}

.restaurant-list {
  flex-wrap: wrap;
  justify-content: center;
  width: auto;
  display: flex;
}

.card {
  cursor: pointer;
  border-radius: 5px;
  width: 240px;
  margin: 20px;
  padding: 10px;
  box-shadow: -1px 5px 10px 5px #2a2a2a33;
}

.card:hover {
  transform: scale(1.05);
}

.card > img {
  border-radius: 10px;
  width: 100%;
}

.card > h5 {
