  font-size: 10px;
}

.restaurant-menu {
  width: auto;
  min-height: 80vh;
  margin-top: 80px;
}

.restaurant-summary {
  background-color: var(--light-black);
  color: var(--white);
  justify-content: center;
  align-items: center;
  height: 200px;
  display: flex;
}

.restaurant-img {
  border-radius: 5px;
  width: 250px;
  height: 170px;
}

.restaurant-summary-details {
  flex-direction: column;
  flex-basis: 520px;
  margin: 20px;
  display: flex;
}

.restaurant-title {
  max-width: 540px;
  font-size: 40px;
  font-weight: 300;
}

.restaurant-tags {
  white-space: nowrap;
  color: inherit;
  opacity: .7;
  max-width: 540px;
  font-size: 15px;
}

.restaurant-details {
  color: inherit;
  justify-content: space-between;
  align-items: center;
  max-width: 340px;
  margin-top: 18px;
  padding-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.restaurant-rating {
  background-color: var(--dark-green);
  border-radius: 5px;
  align-items: center;
  gap: 5px;
  padding: 5px 8px;
  display: flex;
}

.restaurant-menu-content {
  justify-content: center;
  display: flex;
}

.menu-items-container {
  width: 850px;
  margin-top: 30px;
}

.menu-title-wrap {
  padding: 20px;
}

.menu-count {
  color: #282c3f73;
  letter-spacing: -.3px;
  margin-top: 14px;
  font-size: 1rem;
  line-height: 1.3;
