  line-height: 1.3;
}

.item-cost {
  color: #3e4152;
  width: 40%;
  margin-top: 4px;
  font-size: 1rem;
  font-weight: 400;
}

.menu-img-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 200px;
  display: flex;
  overflow: hidden;
