  border-color: var(--dark-orange);
  border-radius: 5px;
  outline: none;
  margin-top: 10px;
  padding: 8px 25px;
}

.shimmer-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

.shimmer-card {
  border-color: #d3d5df;
  flex-basis: 250px;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 0 4px 7px #dadce699;
}

.shimmer-title {
  width: 60%;
  margin-top: 10px;
}

.shimmer-tags {
  width: 80%;
  margin-top: 4px;
}

.shimmer-details {
  width: 100%;
  margin-top: 18px;
}

.shimmer-container .shimmer-img, .restaurant-summary .shimmer-img {
  width: 230px;
  height: 144px;
}

.shimmer-menu-card {
  border-color: #d3d5df;
  justify-content: space-evenly;
  width: 848px;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 7px #dadce699;
}

.shimmer-item-details {
  width: 438px;
}

.shimmer-img-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 118px;
  height: 150px;
  display: flex;
}

.shimmer-img-wrapper .shimmer-img {
  width: 118px;
  height: 96px;
}

.shimmer-img-wrapper .shimmer-btn {
  width: 94px;
  height: 34px;
  margin-top: 10px;
}

.shimmer-w60 {
  width: 60%;
  margin-top: 10px;
}

.shimmer-w40 {
  width: 40%;
  margin-top: 10px;
}

.shimmer-w20 {
  width: 20%;
  margin-top: 4px;
}

.stroke {
  background: #777;
  height: 15px;
}

.stroke-color {
  background: #777;
}

.animate {
  background: linear-gradient(to right, #e6e6e6 5%, #ccc 25%, #e6e6e6 35%) 0 0 / 1000px 100%;
  animation: 3s infinite shimmer;
}

@keyframes shimmer {
  from {
    background-position: -1000px 0;
  }

  to {
    background-position: 1000px 0;
  }
}

.footer {
  text-align: center;
  background-color: var(--footer-bg-color);
  border-radius: 5px;
  width: auto;
  margin: 10px;
  padding: 20px 10px;
  box-shadow: -1px 4px 20px 14px #0003;
}

.footer > i {
  padding: 0 5px;
  font-size: smaller;
}

.fa-heart {
  color: #8b0000;
}

.footer > strong {
  padding-left: 5px;
}

.footer > strong > span {
  color: #cb3600;
}

@media only screen and (width <= 470px) {
  .header {
    height: 60px;
  }

  .header .logo {
    width: 80px;
  }

  .nav-items > ul > li {
    font-size: small;
  }

  .nav-items > ul > li .logout-btn, .nav-items > ul > li .login-btn {
